<template>
  <div id="app">
    <!-- HW 컴포넌트에 데이터, 저장 함수, 비밀번호 체크 함수를 props로 전달합니다. -->
    <HW 
      :duty-data="dutyData" 
      :save-duty-entry="saveDutyEntry"
      :check-password="checkPassword"
    />
  </div>
</template>

<script>
import HW from './components/HW.vue'
import { initializeApp } from "firebase/app";
import { getMessaging, getToken, onMessage } from "firebase/messaging";
import { 
  getFirestore, 
  collection, 
  onSnapshot, 
  query, 
  doc, 
  setDoc,
  where,
  getDocs
} from "firebase/firestore";

export default {
  name: 'App',
  components: {
    HW
  },
  data() {
    return {
      db: null, // Firestore 인스턴스를 저장
      dutyData: [], // Firestore에서 가져온 캘린더 데이터를 저장할 배열      
      unsubscribe: null // 실시간 리스너 해제 함수를 저장
    }
  },
  methods: {
    // 비밀번호 검증 함수
    async checkPassword(inputPassword) {
      if (!this.db) {
        console.error("Firestore DB is not initialized.");
        return { success: false, message: "DB 초기화 오류." };
      }
      
      try {
        // 1. password 필드가 입력된 값과 일치하는 문서를 찾는 쿼리 생성
        const passwordQuery = query(
          collection(this.db, "editpw"),
          where("password", "==", inputPassword)
        );

        // 2. 쿼리 실행
        const querySnapshot = await getDocs(passwordQuery);

        // 3. 결과 확인: 일치하는 문서가 1개 이상 존재하면 성공
        if (!querySnapshot.empty) {
          // 일치하는 문서를 찾았습니다. (실제 데이터는 첫 번째 문서의 필드를 사용할 필요 없이 존재 여부만 확인)
          return { success: true };
        } else {
          // 일치하는 문서가 없거나 비밀번호가 틀린 경우
          return { success: false, message: "비밀번호가 일치하지 않습니다." };
        }
      } catch (error) {
        console.error("비밀번호 확인 중 오류 발생:", error);
        return { success: false, message: `비밀번호 확인 중 오류 발생: ${error.message}` };
      }
    },

    // 당직 정보를 Firestore에 저장 또는 수정하는 함수
    async saveDutyEntry(entry, docIdToUpdate) {
      if (!this.db) {
        console.error("Firestore DB is not initialized.");
        return false;
      }

      // 저장 전에 date 필드를 숫자로 변환 (App.vue에서 숫자로 가정했으므로)
      const dataToSave = {
        ...entry,
        date: parseInt(entry.date, 10), // 'YYMMDD' 문자열을 숫자로 변환
      };

      try {
        if (docIdToUpdate) {
          // 기존 문서 업데이트 (updateDoc 대신 setDoc으로 전체 필드 덮어쓰기)
          const docRef = doc(this.db, "calendar", docIdToUpdate);
          await setDoc(docRef, dataToSave);
          return true; // 수정 성공
        } else {
          // 새 문서 생성 (문서 ID를 date와 category로 조합하여 고정 ID 사용)
          // 기존 문서의 ID가 랜덤 ID이므로, 여기서는 새 문서를 만들 때 date를 키로 하는 고정 ID를 사용하지 않고, setDoc으로 새 문서를 생성합니다.
          // Firebase에서 랜덤 ID가 할당되도록 setDoc 없이 doc(db, 'calendar', newId)를 사용하여 ID를 먼저 만들거나,
          // 가장 간단하게는 setDoc에 ID 없이 docRef를 생성하여 Firestore가 랜덤 ID를 할당하도록 합니다.
          
          // 여기서 우리는 setDoc을 사용하되, 문서 ID를 명시하지 않으면 setDoc은 addDoc처럼 작동하지 않습니다.
          // 따라서, 새 문서를 생성하려면 addDoc을 사용하거나, setDoc을 사용할 경우 고유 ID를 생성해야 합니다.
          
          // 여기서는 문서 ID가 없으므로 Firestore에서 자동으로 생성되도록 합니다.
          const newDocRef = doc(collection(this.db, "calendar"));
          await setDoc(newDocRef, dataToSave);
          return true; // 생성 성공
        }
      } catch (error) {
        console.error("Firestore 저장 중 오류 발생:", error);
        return false;
      }
    }
  },
  mounted() {
    // Firebase 설정
    const firebaseConfig = {
      apiKey: "AIzaSyDMjFwCNFE72bctk4BNodId8fLLXMQ_Ulw",
      authDomain: "hello-notification-121eb.firebaseapp.com",
      projectId: "hello-notification-121eb",
      storageBucket: "hello-notification-121eb.firebasestorage.app",
      messagingSenderId: "17313072285",
      appId: "1:17313072285:web:ddbd170a5b062f236fc074",
      measurementId: "G-8W89T2V4CF"
    };

    // Firebase 초기화 및 데이터 로딩 로직 추가
    const app = initializeApp(firebaseConfig);
    this.db = getFirestore(app); // db 인스턴스를 data에 저장
    const calendarColRef = collection(this.db, "calendar");
    
    // 실시간 리스너 설정 (onSnapshot)
    this.unsubscribe = onSnapshot(query(calendarColRef), (snapshot) => {
      this.dutyData = snapshot.docs.map(doc => {
        const data = doc.data();
        return {
          id: doc.id, // 문서 ID를 추가하여 업데이트 시 사용
          category: data.category,
          date: String(data.date), // HW.vue의 비교를 위해 문자열로 변환하여 전달
          holidayYn: data.holidayYn,
          meYn: data.meYn,
          name: data.name
        };
      });
      console.log("Firestore calendar data loaded successfully.", this.dutyData.length, "items.");
    }, (error) => {
      console.error("Error fetching Firestore data:", error);
    });

    const messaging = getMessaging(app);

    // FCM 로직 (생략)
    onMessage(messaging, (payload) => {
      console.log('Message received: ', payload);
    });

    getToken(messaging, { vapidKey: 'BLYnDWIi8AbuQdrJx1ct6EU5hi6yz6dyG4GxFgLvb_2G42mMjdLwF9i_PkcN8csNR3HynhqVu9ZO92_Am_RcuVg' })
      .then((currentToken) => {
        if (currentToken) {
          console.log("Firebase Token:", currentToken);
        } else {
          console.log('No registration token available. Request permission to generate one.');
        }
      })
      .catch((err) => {
        console.log('An error occurred while retrieving token: ', err);
      });
  },
  beforeUnmount() {
    if (this.unsubscribe) {
      this.unsubscribe();
    }
  }
}
</script>

<style>
body { margin: 0; padding: 0; }
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 0;
}
</style>