  display: flex; flex-direction: column; justify-content: flex-start; align-items: flex-start; 
  padding: 4px 2px 4px 5px; border-radius: 5px; background-color: #fff; border: 1px solid #e0e0e0; 
  transition: transform 0.2s; min-height: 49px;
}

